                    <div class="desc__action action">
                        <a href="assets/specification.pdf" target="_blank" class="action__link btn btn-dark"></a>
                        <br>
                        <span class="action__file_size">PDF 4.5mb</span>
                    </div>
                    <div class="desc__desc text--desc">
                            ,   
                          BiXBiT       , 
                         
                                 - 
                        .
                    </div>
                </div>

            </section>

            <section id="cell" class="cell">
                <h1 class="cell__title text--h1"></h1>
                <div class="cell__desc desc">
                    <p class="desc__desc text--desc">
                           ,    ,   
                                 .   
                            27   GeForce GTX 1070   9 ASIC  Antminer S9.
                    </p>
                    <router-link to="/products/cell" class="desc__link btn btn-link"></router-link>
                </div>

                <ul class="ico_group line">
                    <li class="ico_group__item item">
                        <span class="item__title">750</span>
                        <i class="item__ico ico icon-length"></i>
                        <span class="item__desc"></span>
                    </li>
                    <li class="ico_group__item item">
                        <span class="item__title">440</span>
                        <i class="item__ico ico icon-width"></i>
                        <span class="item__desc"></span>
                    </li>
                    <li class="ico_group__item item">
                        <span class="item__title">380</span>
                        <i class="item__ico ico icon-height"></i>
                        <span class="item__desc"></span>
                    </li>
                </ul>
                <img src="/src/assets/img/prod_cell.png" alt="" class="cell__image">


            </section>

            <section id="rack" class="rack">
                <h1 class="rack__title text--h1"></h1>
                <div class="rack__desc desc">
                    <p class="desc__desc text--desc">
                               
                        .        , 
                           108   36 ASIC.
                    </p>
                    <router-link to="/products/rack" class="desc__link btn btn-link"></router-link>
                </div>

                <ul class="ico_group line">
                    <li class="ico_group__item item">
                        <span class="item__title">626</span>
                        <i class="item__ico ico icon-length"></i>
                        <span class="item__desc"></span>
                    </li>
                    <li class="ico_group__item item">
                        <span class="item__title">750</span>
                        <i class="item__ico ico icon-width"></i>
                        <span class="item__desc"></span>
                    </li>
                    <li class="ico_group__item item">
                        <span class="item__title">2050</span>
                        <i class="item__ico ico icon-height"></i>
                        <span class="item__desc"></span>
                    </li>
                </ul>
                <img src="/src/assets/img/prod_rack.png" alt="  " class="rack__image">
            </section>

            <section id="crisper" class="crisper">
                <h1 class="crisper__title text--h1"></h1>
                <div class="crisper__desc desc">
                    <p class="desc__desc text--desc">
                               .  
                        10-      8   864   288 ASIC.
                                    
                        .       
                        .
                    </p>
                    <router-link to="/products/crisper" class="desc__link btn btn-link"></router-link>
                </div>
                <img src="/src/assets/img/prod_crisper.png" alt="  " class="crisper__image">
                <ul class="ico_group">
                    <li class="ico_group__item item">
                        <i class="item__ico ico icon-bio"></i>
                    </li>
                    <li class="ico_group__item item">
                        <i class="item__ico ico icon-bio"></i>
                    </li>
                    <li class="ico_group__item item">
                        <i class="item__ico ico icon-bio"></i>
                    </li>
                    <li class="ico_group__item item">
                        <i class="item__ico ico icon-bio"></i>
                    </li>

                </ul>
            </section>
        </div>
        <section id="conf" class="conf">
            <h1 class="conf__title text--h1"> </h1>
            <div class="conf__desc desc">
                <div class="desc__desc text--desc">
                           ,  
                          .
                </div>
                <a href="#" class="desc__link btn btn-outline-light"> </a>
                <a class="desc__link btn btn-link" href="/src/assets/specification.pdf" target="_blank"></a>
            </div>

        </section>
    </div>
</template>

<script>
    export default {
        name: "product-all",
        mounted() {
            document.title = "  BiXbiT";

            function checkOffset(el) {
                //centred menu
                $(el).addClass("absolute");

                var top = ($(document).scrollTop() - $(".sticky").offset().top) + (window.innerHeight - $(el).outerHeight()) / 2;
                var start = 10;
                var end = $(".sticky").outerHeight() - $(el).outerHeight() - 10;
                //start point
                top = top <= start ? start : top;
                //end point
                top = top >= end ? end : top;
                $(el).css("top", top + 'px');
            }


            $(document).bind('scroll', function () {
                checkOffset(".float-nav");

            });
            $(document).scrollTop(1);

            $(function () {
                $('.float-nav').onePageNav({
                    currentClass: 'active',
                    changeHash: false,
                    scrollSpeed: 750,
                    filter: '',
                    easing: 'swing',
                    begin: function () {
                        $(".float-nav").hide();
                    },
                    end: function () {
                        $(".float-nav").show();
                    },
                    scrollChange: function ($currentListItem) {

                        //I get fired when you enter a section and I pass the list item of the section
                    }
                });
            });
        },
        destroyed() {
            $(document).unbind('scroll');
        }
    }
</script>

<style scoped>

</style>


