<template>
    <div>
        <section id="product" class="product">

            <ul class="product__images images">
                <li class="images__item item"
                    v-for="img in product.images">
                    <img :src="img" :alt="product.prod_type" class="item_img">
                </li>
            </ul>


            <component :product="product" :is="component"></component>
        </section>


        <section id="product_description" class="product_description">
            <h1 class="product_description__title text--h1">{{product.desc_title}}</h1>
            <p class="product_description__desc text--desc">{{product.desc_desc}}</p>
        </section>
    </div>
</template>

<script>


    import Cell from "./Cell.vue";
    import Crisper from "./Crisper.vue";

    const products = {
        cell: {
            prod_type: "",
            name: "Bixbit",
            width: 750,
            height: 2050,
            length: 626,
            desc_title: "   ",
            desc_desc: "              .     -      ,      1   .\n" +
            "       ,        20-40%.\n" +
            "      ,            ,   BiXBiT.",
            images: ["./src/assets/img/prod_cell.png"],
            asic: {
                min_desc: "  BiXBiT   ,    ,    .        9 ASIC  Antminer S9.",
                default_options: [
                    {name: "- ", type: "text", value: "9"},
                    {name: "  ", type: 'checkbox', value: true},
                    {name: "", type: 'checkbox', value: true},
                    {name: "  ", type: 'checkbox', value: true},
                    {name: " ", type: 'checkbox', value: true},
                    {name: "", type: 'checkbox', value: true}
                ],
                other_options: [
                    {name: "", value: false},
                    {name: " ", value: false},
                    {name: " ", value: false},
                    {name: "", value: false},
                    {name: "", value: false}
                ]
            },
            gpu: {
                min_desc: "  BiXBiT   ,    ,    .        27   GeForce GTX 1070.",
                default_options: [
                    {name: "- ", type: 'text', value: " 27"},
                    {name: " ", value: true},
                    {name: " ", value: true},
                    {name: "  ", value: true},
                    {name: "", value: true},
                    {name: "  ", value: true},
                    {name: "", value: true},
                    {name: " ", value: true},
                    {name: "", value: true},
                ],
                other_options: [
                    {name: "", value: false},
                    {name: " ", value: false},
                    {name: " ", value: false},
                    {name: "", value: false},
                    {name: "", value: false},
                ]
            }

        },
        rack: {
            prod_type: "",
            name: "Bixbit",
            width: 380,
            height: 440,
            length: 750,
            desc_title: "   ",
            desc_desc: "        .      ,         .     ,         .\n" +
            " -    .  ,   ,    ,      ,        -  .\n" +
            "         .       (    100%    ,     )        .         .\n" +
            "      ,         ,   BiXBiT.",
            images: ["./src/assets/img/prod_rack.png"],
            asic: {
                min_desc: "  BiXBiT     ,         .         .         BiXBiT,     36 ASIC ,     .        .",
                default_options: [
                    {name: "- ", type: "text", value: "36"},
                    {name: "  ", type: 'checkbox', value: true},
                    {name: "", type: 'checkbox', value: true},
                    {name: "  ", type: 'checkbox', value: true},
                    {name: "  ", type: 'checkbox', value: true},
                    {name: "", type: 'checkbox', value: true},
                    {name: " ", type: 'checkbox', value: true},
                    {name: " ", type: 'checkbox', value: true},
                    {name: "", type: 'checkbox', value: true},
                    {name: "", type: 'checkbox', value: true}
                ],
                other_options: [
                    {name: "", value: false},
                    {name: "  ", value: false},
                    {name: "DIN-", value: false},
                    {name: "", value: false}
                ]
            },
            gpu: {
                min_desc: "  BiXBiT     ,         .         .         BiXBiT,     108 ,     .        .",
                default_options: [
                    {name: "- ", type: 'text', value: "108"},
                    {name: " ", value: true},
                    {name: " ", value: true},
                    {name: "  ", value: true},
                    {name: "", value: true},
                    {name: "  ", value: true},
                    {name: "   *", value: true},
                    {name: "", value: true},
                    {name: " ", value: true},
                    {name: " ", value: true},
                    {name: "", value: true},
                    {name: "", value: true},
                ],
                other_options: [
                    {name: "", value: false},
                    {name: "  ", value: false},
                    {name: "DIN-", value: false},
                    {name: "", value: false}
                ]
            }

        },
        crisper: {
            prod_type: "",
            name: "Bixbit",
            desc_title: "   ",
            desc_desc: "     16-70 ..,     ,        .           , ,  ,     .\n" +
            "       -       ,     .\n" +
            "    ,    ,          .      .\n" +
            "         .        200 .\n" +
            "   ,  ,      15-20 .",
            images: ["./src/assets/img/prod_crisper.png"],
            type_a:{
                min_desc: "       .         -40  40           .    10     8   768-864      192 ASIC.",
                default_options: [
                    {name: "", type: 'text', value: "3000"},
                    {name: "", type: 'text', value: "2450"},
                    {name: "", type: 'text', value: "2600"},
                ]
            },
            type_b:{
                min_desc: "       .         -40  40           .    10     8   768-864      192 ASIC.",
                default_options: [
                    {name: "", type: 'text', value: "6060"},
                    {name: "", type: 'text', value: "2440"},
                    {name: "", type: 'text', value: "2600"},
                ]
            },
            type_c:{
                min_desc: "       .         -40  40           .    10     8   768-864      192 ASIC.",
                default_options: [
                    {name: "", type: 'text', value: "12190"},
                    {name: "", type: 'text', value: "2440"},
                    {name: "", type: 'text', value: "2600"},
                ]
            }


        }
    };
    export default {
        name: "product-more",

        components: {
            "Cell": Cell,
            "Crisper": Crisper
        },
        data() {
            return {
                product: {
                    asic: {},
                    gpu: {},
                },
                device: 'gpu',
                options: 'empty',
                component: "Cell",
                products: {}
            }
        },
        mounted() {
            if (this.$route.name == 'Cell') {
                this.product = products.cell;
                this.component = "Cell";
            }
            if (this.$route.name == 'Rack') {
                this.product = products.rack;
                this.component = "Cell";
            }
            if (this.$route.name == 'Crisper') {
                this.product = products.crisper;
                this.component = "Crisper";
            }
            document.title = this.product.prod_type+" "+this.product.name;
        }
    }
</script>

<style scoped>

</style>


